import { shallowEqual } from "react-redux";
import { getToDos } from "store/thunks/management";
import { MToDo } from "store/types/models/management";
import useLoadAndSelect from "../useLoadAndSelect";

function useToDos(delimiter?: MToDo['_id']) {
    return useLoadAndSelect({
        actionCreator: getToDos,
        postParams: delimiter,
        selector: state => Object.values(state.management.toDos),
        equality: shallowEqual,
        deduplicate: ['toDos', delimiter || 'top']
    })
}

export default useToDos