import FalconCardHeader from 'components/FalconCardHeader';
import ButtonIcon from 'components/ButtonIcon';
import { v1 as uuid } from 'uuid'
import { useSelector } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Circle } from 'react-es6-progressbar.js';

////// Local Components //////////
import AddTaskModalWithButton from './AddTaskModalWithButton'
import { StyleConfig } from 'components/gantt/helpers/config/Config';
import AssignmentAvatars from "layouts/workflows/stage/tasks/AssignmentAvatars";
////// Global Components /////////

////// CommonJS Helpers //////////
import {BORDER_COLORS, LIGHT_DARK_MAP, NAVY_COLOR_PALATTE, PASTEL_COLORS} from 'layouts/management/assignments/ColorPicker'

////// Custom Hooks //////////////
import useSetting from 'store/hooks/settings/useSettings';
import dayjs from 'dayjs';
import { MODE, VIEW_MODE } from 'components/gantt/Const';
import { grays, themeColors } from 'helpers/utils';
import useStage from 'store/hooks/workflow/useStage'
import { MMilestone, MTask } from 'store/types/models/workflow';
import { Populate } from 'store/types/models/common';
import Flex from 'components/Flex';
import AvatarGroup from 'components/AvatarGroup';

