////// Types /////////////////////
interface Props {
    
}

const DEFAULT_TASKS = [
    {
        id: 'first',
        name: 'First Task',
        start: '2021-06-20',
        end: '2021-06-26',
        color: PASTEL_COLORS.GREEN
    },
    {
        id: 'second',
        name: 'Second Task',
        start: '2021-06-27',
        end: '2021-07-02',
        color: PASTEL_COLORS.BLUE
    },
    {
        id: 'third',
        name: 'Third Task',
        start: '2021-06-25',
        end: '2021-07-03',
        color: PASTEL_COLORS.SAND
    },
    {
        id: 'fourth',
        name: 'Fourth Task',
        start: '2021-07-02',
        end: '2021-07-07',
        color: PASTEL_COLORS.PINK
    },
    {
        id: 'fifth',
        name: 'Fifth Task',
        start: '2021-07-02',
        end: '2021-07-07',
        color: PASTEL_COLORS.LAVENDER
    }
]

const DEFAULT_LINKS = [
    {
        id: 'link-1-2',
        start: 'first',
        end: 'second'
    }
]

const TimelineTest: FC<Props> = () => {
    const { value: stage } = useStage("60956beed0085e1978fc395f")
    const tasks = useSelector( state => state.workflow.tasks );
    const [dayWidth, setDayWidth] = useState(40);
    const milestones = useMemo(() => stage?.milestones.map( m => ({...m, tasks: m.tasks.map( taskId => tasks[taskId] ).filter(s=>!!s)})) || [], [stage, tasks])

    function renderMilestone(milestone: Populate<MMilestone, 'tasks', MTask[]>, height: number) {
        return (
            <Container fluid>
                <Row style={{height}}>
                    <Col tag={Flex} xs="auto" align="center">
                        <FontAwesomeIcon icon="level-up-alt" transform="rotate-90" />
                    </Col>
                    <Col tag={Flex} align="center">
                        <h6 className="my-0 py-0">{milestone.title}</h6>
                    </Col>
                </Row>
            </Container>
        )
    }

    const taskProgressOptions = {
        color: themeColors.primary,
        progress: 0,
        strokeWidth: 11,
        trailWidth: 11,
        trailColor: grays['200'],
        easing: 'easeInOut',
        duration: 2000,
        svgStyle: {
          'stroke-linecap': 'round',
          display: 'block',
          width: '100%'
        },
        text: { autoStyleContainer: false },
        // Set default step function for all animate calls
        step: (state, circle) => {
          const percentage = Math.round(circle.value() * 100);
          circle.setText("<span class='value'>"+percentage+"</span>");
        }
    };

    function renderTask(task: MTask, height: number) {
        return (
            <Container fluid>
                <Row style={{height}}>
                    <Col tag={Flex} align="center" >
                        <p className="fs--1 my-0 py-0">{task.title}</p>
                    </Col>
                    <Col tag={Flex} align="center" xs={2}>
                        <p className="fs--1 my-0 py-0">{dayjs(task.startDate).format('M/D/YY')}</p>
                    </Col>
                    <Col tag={Flex} align="center" xs={2}>
                        <p className="fs--1 my-0 py-0">{dayjs(task.endDate).format('M/D/YY')}</p>
                    </Col>
                    <Col tag={Flex} align="center" xs={2}>
                        <Circle
                            progress={ task.progress[task.progress.length - 1]?.percent / 100 || 0 }
                            options={ taskProgressOptions }
                            container_class="progress-circle progress-circle-taskmenu"
                            container_style={{ width: (height * 0.67) + 'px', height: (height * 0.67) + 'px' }}
                        />
                    </Col>
                </Row>
            </Container>
        )
    }

    const allTasks = useMemo(() => milestones?.reduce( (all, milestone) => [
            ...all, 
            {
                ...milestone, 
                id: milestone._id,
                name: milestone.title,
                start: milestone.startDate!,
                end: milestone.endDate!,
                rowStyle: {
                    backgroundColor: 'rgba(150, 150, 190, 0.1)',
                    borderBottom: '2px solid black'
                },
                tableContent: renderMilestone,
                color: 'rgb(245, 148, 134)'
            }, 
            ...milestone.tasks.map( task => ({
                ...task,
                id: task._id,
                name: task.title,
                start: task.startDate!,
                end: task.endDate!,
                tableContent: renderTask,
                color: task.progress[task.progress.length - 1]?.percent === 100 ? themeColors.success : 'rgb(145, 148, 184)'
            }))
        ], [] as any[])
    , [milestones])

    //const [tasks, setTasks] = useState<ITask[]>(DEFAULT_TASKS);
    const [links, setLinks] = useState<ILink[]>(DEFAULT_LINKS);
    const [mode, setMode] = useState(VIEW_MODE.YEAR);
    const [itemHeight, setItemHeight] = useState(40);
    const [ linkMode, setLinkMode ] = useState(false)

    const [ selectedItems, setSelectedItems ] = useState<(ITask | ILink)[]>([])

    const isDark = useSetting('isDark');

    // useEffect(() => {
    //     const colors = Object.values(NAVY_COLOR_PALATTE)
    //     fetch("https://jsonplaceholder.typicode.com/todos")
    //         .then( data => data?.json())
    //         .then( data => setTasks(data?.map( (d: any) => {
    //             const start = dayjs().add(Math.random()*100, 'day')
    //             const end = start.add(Math.random()*25, 'day')
    //             return {
    //                 id: d.id,
    //                 name: d.title,
    //                 start: start.toISOString(),
    //                 end: end.toISOString(),
    //                 color: colors[Math.floor((Math.random()*80)) % colors.length]
    //             }
    //         })))
    // }, [])


    const timelineConfig: StyleConfig = {
        header: {
            top: {
                style: {}
            },
            middle: {
                style: {},
                selectedStyle: {} 
            },
            bottom: {
                style: {},
                selectedStyle: {} 
            }
        }
    }

    const correctedTasks = /* isDark 
                            ? tasks.map( t => {
                                const color = LIGHT_DARK_MAP.find( color => color.light === t.color)?.dark;
                                const borderColor = color && BORDER_COLORS[color]
                                return {
                                    ...t, 
                                    color,
                                    borderColor
                                }
                            }) 
                            :  */tasks;

    return (
        <Card>
            <CardHeader className="bg-light">
                <Row>
                    <Col>
                        <h3>Workflow Timelines</h3>
                    </Col>
                </Row>
                <hr/>
                <Row >
                    <Col xs="auto">
                        <h6>Row Height:</h6>
                    </Col>
                    <Col>
                        <CustomInput id="item-height-selector" step="1" min="20" max="50" type="range" value={itemHeight} onChange={e => setItemHeight(+e.target.value)} /> 
                    </Col>
                    <Col xs="auto" className="border-left">
                        <h6>Time Span:</h6>
                    </Col>
                    <Col className="border-right">
                        <CustomInput id="day-width-selector" step="1" min="1" max="100" type="range" value={dayWidth} onChange={e => setDayWidth(+e.target.value)} />
                    </Col>
                    <Col xs="auto">
                        <CustomInput 
                            id="link-mode" 
                            type="checkbox"
                            label="Link Mode"
                            checked={linkMode} 
                            onChange={e=>setLinkMode(e.target.checked)} 
                        />
                    </Col>
                    <Col xs="auto">
                        <ButtonGroup size="sm">
                            <Button onClick={()=>setMode(VIEW_MODE.DAY)} color="primary">Day</Button>
                            <Button onClick={()=>setMode(VIEW_MODE.WEEK)} color="primary">Week</Button>
                            <Button onClick={()=>setMode(VIEW_MODE.MONTH)} color="primary">Month</Button>
                            <Button onClick={()=>setMode(VIEW_MODE.YEAR)} color="primary">Year</Button>
                        </ButtonGroup>
                    </Col>
                    <Col xs="auto">
                        <Button onClick={()=>setSelectedItems([])} size="sm">Clear Selections</Button>
                    </Col>
                </Row>
                
                
                
            </CardHeader>
            <CardBody>
                <TimeLine 
                    data={allTasks}
                    links={links}
                    onCreateLink={link => setLinks( prev => [...prev, {id: uuid(), start: link.start.task.id, end: link.end.task.id}] )}
                    onDeleteLink={ link => setLinks( prev => prev.filter( l => link.id !== l.id))}
                    onUpdateTask={ (task, update) => {
                        console.log(task, update)
                    }}
                    onSelectItem={ item => setSelectedItems( i => i.find( it => it.id === item.id ) ? i.filter( it => it.id !== item.id) : [...i, item] ) }
                    selectedItems={ selectedItems }
                    config={timelineConfig}
                    itemHeight={itemHeight}
                    mode={mode}
                    shouldLink={linkMode}
                    dayWidth={1471.8 * Math.exp(-0.079 * dayWidth)}
                />
            </CardBody>
        </Card>
        
    )
}

export default TimelineTest

