import { stages } from 'store/thunks/workflow/stages'
import { MStage } from 'store/types/models/workflow';
import useLoadAndSelect from 'store/hooks/useLoadAndSelect'

function useStage (stageId: MStage['_id']) {
    return useLoadAndSelect<MStage>({
        actionCreator: stages.getOne,
        postParams: stageId,
        selector: state => state.workflow.stages[stageId],
        deduplicate: ['getStageDetails', stageId]
    })
}

export default useStage;