////// Packages //////////////////
import BounceSpinner from 'components/BounceSpinner'
import React, { FC } from 'react'
import { Button, ButtonProps } from 'reactstrap'
////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

interface Props extends ButtonProps {
    text?: string;
    submitting?: boolean;
}

const SubmitButton: FC<Props> = ({text='Submit', submitting, color="primary", ...rest}) => {
    
    
    return (
        <Button color={color} {...rest}>
            {text}
            {submitting && <BounceSpinner color="light" size="sm" /> }
        </Button>
    )
}

export default SubmitButton