export const update: ActionCreator<['updateTask']> = (updateObject: Partial<MTask> & {taskId: MTask['_id']; progress?: number; status?: string}) => {
    const actions = [{
        name: 'updateTask',
        data: updateObject
    }] as const;
    return { actions }
}
tasks.update = update;

export const addTaskDocuments: ActionCreator<['addTaskDocuments']> = async ( attachments: DropzoneFile[], taskId: MTask['_id']) => {

    const arrDocumentsPosted = [] as {location: string, thumb?: {location: string}, title: string}[]

    for ( const attachment of attachments ) {        
        const newDoc = await uploadDoc(base64toBlob(attachment.base64), 'document', attachment.path)
        if (newDoc?.success && newDoc.files?.document) {
            arrDocumentsPosted.push(
                {
                    location: newDoc.files?.document.name, 
                    thumb: newDoc.files?.document.thumb 
                                ? {location: newDoc.files?.document.thumb} 
                                : undefined, 
                    title: attachment.path
                }
            )
        }
    }

    if (!attachments || !attachments.length) return { cancel: true }
  
    const actions = [{ 
        name: 'addTaskDocuments',
        data: {
            documents: arrDocumentsPosted,
            taskId
        }
    }] as const;
    return {actions}
}
tasks.addTaskDocuments = addTaskDocuments;

export const createAssignment: ActionCreator<['createAssignment']> = ( taskId: MTask['_id'], assignees: MPerson['_id'][], description: string, assignedHours: number, dueDate: string) => {
    const actions = [{
        name: 'createAssignment',
        data: {
            taskId,
            assignment: {
                assignees,
                description,
                assignedHours,
                dueDate
            }
        }
    }] as const;
    return { actions }
}
tasks.createAssignment = createAssignment;

interface IUpdateAssignmentOptions {
    status?: ITaskStatus;
    complete?: boolean;
    assignees?: MPerson['_id'][];
    description?: string;
    assignedHours?: number;
    dueDate?: string;
}

export const updateAssignment: ActionCreator<['updateAssignment']> = ( assignmentId: MAssignment['_id'], assignmentUpdate: IUpdateAssignmentOptions) => {
    const actions = [{
        name: 'updateAssignment',
        data: {
            assignmentId,
            ...assignmentUpdate
        }
    }] as const;
    return { actions }
}
tasks.updateAssignment = updateAssignment;

export const getPersonAssignments: ActionCreator<["getPersonAssignments"]> = (personId: MPerson['_id']) => {
    const actions = [{
        name: 'getPersonAssignments',
        data: {
            personId
        }
    }] as const;
    return { actions }
}
tasks.getPersonAssignments = getPersonAssignments;

interface ReorderAssignmentProp {
    assignmentId: MAssignment['_id'];
    sortIndex: number;
    category: string;
}

export const reorderAssignments: ActionCreator<"reorderAssignment"[]> = (items: ReorderAssignmentProp[]) => {
    const actions = items.map( data => ({
        name: 'reorderAssignment',
        data
    } as const))
    return { actions }
}
tasks.reorderAssignments = reorderAssignments