import Flex from 'components/Flex';
import React, { FC, useState } from 'react';
import { Button, Row, Col, ModalBody, Modal, ModalHeader, ModalFooter } from 'reactstrap';
import ButtonIcon from 'components/ButtonIcon';
import FormGroupInput from 'components/FormGroupInput';
import useAction from 'store/hooks/useAction';
import { setKanbanCategories } from 'store/thunks/settings';
import { toast } from 'react-toastify';

interface Props {
  categories: string[]
}

const KanbanHeader: FC<Props> = ({categories}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [title, setTitle] = useState<string>();
  const setCategories = useAction( setKanbanCategories )

  const handleSubmit = async () => {
    const data = await setCategories.post( Array.from(new Set([...categories, title])) )
    if (data?.success) {
      toast.success('Added category')
      setIsOpen(false)
      setTitle(undefined)
    } else {
      toast.error(data?.error || 'Something went wrong')
    }
  }

  return (
    <Row noGutters className="bg-100 rounded-soft px-card py-2 mt-2 mb-3 btn-reveal-trigger">
      <Col tag={Flex} align="center" justify="between">
        <h5 className="mb-0">Assignments</h5>
        <ButtonIcon
          id="add-kanban-category"
          color="link"
          icon="plus" 
          iconAlign="right"
          size="sm"
          className="btn-reveal text-decoration-none"
          onClick={() => setIsOpen(true)}
          
        >
          Add Category
        </ButtonIcon>
        <Modal isOpen={isOpen} toggle={()=>setIsOpen(false)}>
          <ModalHeader className="bg-light border-bottom-0">
            Add Category
          </ModalHeader>
          <ModalBody>
            <FormGroupInput
              id="new-category-title"
              label="Title"
              value={title}
              onChange={e=>setTitle(e.target.value)}
            />
          </ModalBody>
          <ModalFooter tag={Flex} align="bottom" justify="end" className="bg-light border-top-0">
            <Button color="primary" onClick={handleSubmit}>
              Submit
            </Button>
          </ModalFooter>
        </Modal>
      </Col>
    </Row>
  );
};

export default KanbanHeader;
