import { BaseDuck } from "store/ducks/BaseDuck";
import { toDos as toDosDuck } from "store/ducks/management";
import { ActionCreator } from "store/types/interface/common";
import { MToDo } from "store/types/models/management";

interface ToDoController extends BaseDuck<MToDo> {
    createToDo: ActionCreator<['createToDo']>;
    editToDo: ActionCreator<['editToDo']>;
    deleteToDo: ActionCreator<['deleteToDo']>;
    getToDos: ActionCreator<['getToDos']>;
    reorderToDos: ActionCreator<"editToDo"[]>;
}

export const toDos = toDosDuck as ToDoController

export const createToDo: ActionCreator<['createToDo']> = (
    description: string, 
    kanban: {category?: string, sortIndex: number}, 
    dueDate?: string,
    color: string
) => {
    const actions = [{
        name: 'createToDo',
        data: {
            description,
            dueDate,
            kanban,
            color
        }
    }] as const
    return { actions }
}

toDos.createToDo = createToDo;

interface EditToDoProps {
    description?: string, 
    dueDate?: string, 
    kanban?: {
        category?: string, 
        sortIndex?: number,
        color?: string;
    },
    complete?: boolean
}

export const editToDo: ActionCreator<['editToDo']> = (toDoId: MToDo['_id'], props: EditToDoProps) => {
    const actions = [{
        name: 'editToDo',
        data: {
            toDoId,
            ...props
        }
    }] as const
    return { actions }
}

export const deleteToDo: ActionCreator<['deleteToDo']> = (toDoId: MToDo['_id']) => {
    const actions = [{
        name: 'deleteToDo',
        data: {
            toDoId
        }
    }] as const
    return { actions }
}

export const getToDos: ActionCreator<['getToDos']> = (delimiter?: MToDo['_id']) => {
    const actions = [{
        name: 'getToDos',
        data: {
            delimiter
        }
    }] as const
    return { actions }
}

interface ReorderToDoProp {
    toDoId: MToDo['_id'];
    sortIndex: number;
    category: string;
}

export const reorderToDos: ActionCreator<"editToDo"[]> = (items: ReorderToDoProp[]) => {
    const actions = items.map( i => ({
        name: 'editToDo',
        data: {
            toDoId: i.toDoId,
            kanban: {
                sortIndex: i.sortIndex,
                category: i.category
            }
        }
    } as const))
    return { actions }
}
toDos.reorderToDos = reorderToDos;