import { getKanbanCategories } from "store/thunks/settings";
import useLoadAndSelect from "../useLoadAndSelect";

function useKanbanCategories() {
    return useLoadAndSelect({
        actionCreator: getKanbanCategories,
        selector: state => state.app.options?.kanban?.categories,
        deduplicate: ['kanbanCategories']
    })
}

export default useKanbanCategories;