import DateHelper from '../helpers/DateHelper'
const HORIZON_BUFFER = 1000;
const HORIZON_BUFFER_ALERT = 750;


export default class DataController {
  lower_limit: number;
  lower_data_limit?: number;

  upper_limit: number;
  upper_data_limit?: number;

  dataToRender: unknown[];

  nowPosition?: number; //?  
  dayWidth?: number;

  onHorizonChange?: (lLimit: Date, hLimit: Date)=>void;

  constructor() {
    this.lower_limit = 0;
    this.upper_limit = 0;
    this.dataToRender = [];
  }

  initialise = (start: number, end: number, nowPosition: number, dayWidth: number) => {
    this.nowPosition = nowPosition;
    this.dayWidth = dayWidth;
    this.setLimits(start, end);
    this.loadDataHorizon();
  };

  //OnScroll
  setStartEnd = (start: number, end: number, nowPosition: number, dayWidth: number) => {
    this.nowPosition = nowPosition;
    this.dayWidth = dayWidth;
    if (this.needData(start, end)) {
      this.setLimits(start, end);
      this.loadDataHorizon();
    }
  };

  needData = (start: number, end: number) => {
    if (!this.lower_data_limit || !this.upper_data_limit) return console.log('Data Controller not initialized before load horizon')
    return start < this.lower_data_limit || end > this.upper_data_limit;
  };

  setLimits = (start: number, end: number) => {
    this.lower_limit = start - HORIZON_BUFFER;
    this.lower_data_limit = start - HORIZON_BUFFER_ALERT;
    this.upper_limit = end + HORIZON_BUFFER;
    this.upper_data_limit = end + HORIZON_BUFFER_ALERT;
  };

  //OnScroll
  loadDataHorizon = () => {
    if (!this.nowPosition || !this.dayWidth) return console.log('Data Controller not initialized before load horizon')
    const lowerLimit = DateHelper.pixelToDate(this.lower_limit, this.nowPosition, this.dayWidth);
    const upLimit = DateHelper.pixelToDate(this.upper_limit, this.nowPosition, this.dayWidth);
    this.onHorizonChange && this.onHorizonChange(lowerLimit, upLimit);
  };
}