////// Packages //////////////////
import React, { FC, useState } from 'react'
import { toast } from 'react-toastify';
import { Modal, ModalBody, ModalFooter, ModalHeader, FormGroup, Label } from 'reactstrap'

////// Local Components //////////
import ColorPicker, { PASTEL_COLORS } from './ColorPicker';

////// Global Components /////////
import FormGroupInput from 'components/FormGroupInput'
import DateTimeInputGroup from 'components/datetime/DateTimeInputGroup';
import SubmitButton from 'compositions/SubmitButton';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { createToDo } from 'store/thunks/management';


////// Types /////////////////////

interface Props {
    category: string;
    index: number;
    isOpen: boolean,
    setIsOpen: React.Dispatch<React.SetStateAction<boolean>>
}

const AddToDo: FC<Props> = ({category, index, isOpen, setIsOpen}) => {
    const [description, setDescription] = useState<string>();
    const [dueDate, setDueDate] = useState<string>();
    const [color, setColor] = useState(PASTEL_COLORS[0]);
    const createToDoAction = useAction( createToDo )

    async function handleSubmit() {
        const data = await createToDoAction.post(description, {category, sortIndex: index}, dueDate, color)
        if (data?.success) {
            toast.success('Created new to-do');
            setIsOpen(false)
        } else {
            toast.error(data?.error || "Something went wrong")
        }
    }

    return (
        <Modal isOpen={isOpen} toggle={()=>setIsOpen(false)}>
            <ModalHeader className='bg-light border-bottom-0'>
                Add To-Do
            </ModalHeader>
            <ModalBody>
                <FormGroupInput
                    id="todo-description"
                    type="textarea"
                    placeholder="Enter description..."
                    rows={ 4 }
                    value={ description }
                    onChange={ e => setDescription(e.target.value) }
                />
                <FormGroup>
                    <Label for="todo-duedate">Due Date</Label>
                    <DateTimeInputGroup
                        timeFormat={false}
                        value={ dueDate }
                        onChange={ date => setDueDate(date) }
                    />
                </FormGroup>
                <ColorPicker color={color} setColor={setColor} />
            </ModalBody>
            <ModalFooter className='bg-light border-top-0'>
                <SubmitButton
                    submitting={createToDoAction.loading}
                    onClick={handleSubmit}
                />
            </ModalFooter>
        </Modal>
        
    )
}

export default AddToDo;