////// Packages //////////////////
import React, { FC, useContext } from 'react';
import { Draggable, DropResult } from 'react-beautiful-dnd';
import {
  Card,
  CardBody,
  Badge,
  CardHeader,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledTooltip
} from 'reactstrap';
import { Link, useHistory } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { shallowEqual, useSelector } from 'react-redux';
////// Local Components //////////

////// Global Components /////////
import AvatarGroup from 'components/AvatarGroup';
import Flex from 'components/Flex';

////// CommonJS Helpers //////////
import { getItemStyle } from './helpers';

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////
import { AssignmentWithTaskInfo, MAssignment } from 'store/types/models/workflow';
import { statusColorsMap } from 'helpers/statusColorsMap';
import dayjs from 'dayjs';
import usePeopleList from 'store/hooks/person/usePeopleList';



export interface KanbanAssignment extends AssignmentWithTaskInfo {
  type: 'assignment'
}

interface Props {
    assignment: KanbanAssignment;
    category: string;
    index: number;
    lastIndex: number;
    fakeDragEvent: (result: Partial<DropResult>)=>void;
    categories: string[];
}

const AssignmentCard: FC<Props> = ({ assignment, category, index, lastIndex, fakeDragEvent, categories }) => {
  const loginPersonId = useSelector( state => state.app.login.personId );

  const assignedIds = assignment.assignees.map(a => a.personId)
  const {value: people} = usePeopleList(...assignedIds)
  const history = useHistory()

  const draggableId = `draggable-assignment-${assignment._id}`
  return (
    <Draggable draggableId={draggableId} index={index}>
      {(provided, snapshot) => (
        <div
          className="kanban-item"
          ref={provided.innerRef}
          {...provided.draggableProps}
          {...provided.dragHandleProps}
        >
          <Card
            className="kanban-item-card"
            style={getItemStyle(snapshot.isDragging)}
          >
            <CardBody>
              <Flex justify="between">
                <div>
                  <h6 className="mb-1">{assignment.stage.identifier}</h6>
                  <p className="fs--2 my-0"><i>{assignment.stage.title}</i></p>
                </div>
                <div className="mb-2">
                  <Badge 
                    className={`badge-soft-${statusColorsMap[assignment.status[assignment.status.length - 1].title]} d-inline-block py-1 mr-1 mb-1`} 
                    key={index + 10}
                  >
                    {assignment.status[assignment.status.length - 1].title}
                  </Badge>
                </div>
              </Flex>
              
              <hr className="my-1" />
              <h6 className="mb-1"><i>{assignment.task.title}</i></h6>
              { assignment.description?.split('\n').map((str, i) => <p className="mb-1 fs--1" key={i}>{str}</p>) }
              
              <div className="kanban-item-footer">
                <div className="text-500">
                  {dayjs(assignment.dueDate).format("MM/DD/YYYY")}
                </div>
                <div>
                  <AvatarGroup avatarSize="m" showMembers={ 6 } people={ people || []} />
                </div>
                <UncontrolledDropdown
                  className="text-sans-serif"
                  onClick={e => {
                    e.stopPropagation();
                  }}
                >
                  <DropdownToggle color="falcon-default" size="sm" className="py-0 px-2">
                    <FontAwesomeIcon icon="ellipsis-h" />
                  </DropdownToggle>
                  <DropdownMenu right className="py-0">
                    {index > 0 && 
                      <DropdownItem
                        onClick={()=>fakeDragEvent(
                          {
                            reason: 'DROP', 
                            draggableId,
                            destination: {
                              droppableId: category, 
                              index: index - 1
                            }, 
                            source: {
                              droppableId: category, 
                              index: index
                            }
                          }
                        )}
                      >Move Up</DropdownItem>
                    }
                    {index < lastIndex && 
                      <DropdownItem
                        onClick={()=>fakeDragEvent(
                          {
                            reason: 'DROP', 
                            draggableId,
                            destination: {
                              droppableId: category, 
                              index: index + 1
                            }, 
                            source: {
                              droppableId: category, 
                              index: index
                            }
                          }
                        )}
                      >
                        Move Down
                      </DropdownItem>
                    }
                    {!assignment.complete && 
                      <>
                        <DropdownItem divider />
                        {categories.filter(c => c !== category).map( (c, i) => (
                          <DropdownItem
                            key={i}
                            onClick={()=>fakeDragEvent(
                              {
                                reason: 'DROP',
                                draggableId,
                                destination: {
                                  droppableId: c, 
                                  index: 0
                                }, 
                                source: {
                                  droppableId: category, 
                                  index: index
                                }
                              }
                            )}
                          >
                            {c}
                          </DropdownItem>
                        ))}
                      </>
                    }
                    
                    <DropdownItem divider />
                    <DropdownItem 
                      className="text-primary"
                      onClick={() => history.push(`/workflows/${assignment.stage.workflowId}/stage/${assignment.stage._id}/task/${assignment.task._id}`)}
                    >
                      Go To Task
                    </DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
              </div>
              
            </CardBody>
          </Card>
        </div>
      )}
    </Draggable>
  );
};

export default AssignmentCard;
