import React, {  useState, FC, useEffect } from 'react';
import classNames from 'classnames';

import KanbanColumnHeader from './KanbanColumnHeader';
import { Droppable, DropResult } from 'react-beautiful-dnd';

import AddToDo from './AddToDo';
import ButtonIcon from 'components/ButtonIcon';
import AssignmentCard, { KanbanAssignment } from './AssignmentCard';
import ToDoCard from './ToDoCard';
import { MToDo } from 'store/types/models/management';

export interface KanbanTodo extends MToDo {
  type: 'todo';
}

export type KanbanConfiguredAssignment = KanbanAssignment & {kanban: {category: string, sortIndex: number}}
export type KanbanItem = KanbanConfiguredAssignment | KanbanTodo;

interface Props {
  category: string;
  index: number;
  items: KanbanItem[];
  categories: string[];
  fakeDragEvent: (result: DropResult)=>void
}

const KanbanColumn: FC<Props> = ({ category, index, items, categories, fakeDragEvent }) => {
  const [showForm, setShowForm] = useState(false);

  // useEffect(() => {
  //   const kanbanContainer = document.getElementById(`container-${index}`)!;
  //   kanbanContainer.scrollTop = kanbanContainer.scrollHeight;
  // }, [showForm, index]);

  const categoryItems = items.sort( (a, b) => {
    const sorta = a.kanban.sortIndex || 0
    const sortb = b.kanban.sortIndex || 0
    if (sorta === sortb) {
      if (a._id > b._id) {
        return 1
      } else {
        return -1
      }
    } else if (sorta > sortb) {
      return 1
    }
    return -1
  })


  return (
    <div className={classNames('kanban-column', { 'form-added': showForm })}>
      <KanbanColumnHeader category={category} items={items} categories={categories} />
      <Droppable droppableId={category}>
        {(provided, snapshot) => (
          <>
            <div
              className="kanban-items-container scrollbar"
              id={`container-${index}`}
              ref={provided.innerRef}
              {...provided.droppableProps}
            >
              {
                categoryItems.map( (item, i, a) => {
                  switch (item.type) {
                    case 'assignment':
                      return <AssignmentCard 
                              categories={categories} 
                              assignment={item} 
                              category={category} 
                              index={i} key={item._id} 
                              lastIndex={a.length - 1} 
                              fakeDragEvent={fakeDragEvent}
                            />
                    case 'todo':
                      return <ToDoCard 
                              toDo={item} 
                              category={category} 
                              index={i} 
                              key={item._id} 
                              lastIndex={a.length - 1} 
                              fakeDragEvent={fakeDragEvent} 
                              categories={categories}
                            /> 
                  }
                })
              }
              {showForm && <AddToDo category={category} index={categoryItems.length} isOpen={showForm} setIsOpen={setShowForm} />}
              {provided.placeholder}
            </div>
            {!showForm && (
              <div className="kanban-column-footer">
                <ButtonIcon
                  className="btn-add-card text-600 text-decoration-none"
                  color="link"
                  block
                  icon="plus"
                  iconClassName="mr-1"
                  size="sm"
                  onClick={() => {
                    setShowForm(true);
                  }}
                >
                  Add a Personal To-Do
                </ButtonIcon>
              </div>
            )}
          </>
        )}
      </Droppable>
    </div>
  );
};
export default KanbanColumn;
