////// Packages //////////////////
import React, { FC, useEffect, useState } from 'react'
import useAction from 'store/hooks/useAction';
import { editToDo } from 'store/thunks/management';
import { Modal, ModalHeader, ModalFooter, ModalBody, FormGroup, Label} from 'reactstrap'
import { KanbanTodo } from './KanbanColumn';
////// Local Components //////////
import ColorPicker, {PASTEL_COLORS} from './ColorPicker'

////// Global Components /////////
import FormGroupInput from 'components/FormGroupInput';
import DateTimeInputGroup from 'components/datetime/DateTimeInputGroup';
import SubmitButton from 'compositions/SubmitButton';
import Flex from 'components/Flex';
import { toast } from 'react-toastify';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

interface Props {
    toDo: KanbanTodo;
    setIsOpen: React.Dispatch<React.SetStateAction<boolean>>;
}

const EditToDo: FC<Props> = ({toDo, setIsOpen}) => {
    const [description, setDescription] = useState<string>()
    const [dueDate, setDueDate] = useState<string>();
    const [color, setColor] = useState<string>(PASTEL_COLORS.SAND);
    const editAction = useAction( editToDo );

    useEffect(() => {setDescription(toDo.description)}, [toDo.description])
    useEffect(() => {setDueDate(toDo.dueDate)}, [toDo.dueDate])


    async function handleSubmitEdit() {
        const data = await editAction.post(toDo._id, {description, dueDate, kanban: {color}})
        if (data?.success) {
            toast.success('Updated to-do');
            setIsOpen(false);
        } else {
            toast.error(data?.error || "Something went wrong");
        }
    }

    return (
        <Modal isOpen toggle={()=>setIsOpen(false)}>
            <ModalHeader>

            </ModalHeader>
            <ModalBody>
                <FormGroupInput 
                    id="todo-description"
                    type="textarea"
                    placeholder="Enter description..."
                    rows={ 4 }
                    value={ description }
                    onChange={ e => setDescription(e.target.value) }
                />
                <FormGroup>
                    <Label for="todo-duedate">Due Date</Label>
                    <DateTimeInputGroup
                        timeFormat={false}
                        value={ dueDate }
                        onChange={ date => setDueDate(date) }
                    />
                </FormGroup>
                <ColorPicker color={color} setColor={setColor} />
            </ModalBody>
            <ModalFooter tag={Flex} align="bottom" justify="end">
                <SubmitButton
                    submitting={ editAction.loading }
                    onClick={handleSubmitEdit}
                />
            </ModalFooter>
        </Modal>
    )
}

export default EditToDo