import React, { FC } from 'react';
import Loader from './Loader';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Col, Row } from 'reactstrap';

interface Props {
    dataLoader: {loading?: boolean; error?: any;};
    size?: 's' | 'm' | 'l' | 'xl';
    children: any;
    className?: string;
}

const LoadingGateway: FC<Props> = ({dataLoader, size='l', className, children}) => {
    const sizeClass = {
        s: 'fs-0',
        m: 'fs-1',
        l: 'fs-2',
        xl: 'fs-4'
    }

    if (dataLoader?.loading) {
        return <Loader />
    }

    if (dataLoader?.error) {
        return ( 
            <>
                <Row className={"flex-center py-5 " + className}>
                    <Col xs="auto">
                        <FontAwesomeIcon icon='exclamation-circle' className={[sizeClass, 'text-danger'].join(' ')} />
                    </Col>
                </Row>
                <Row className={"flex-center py-5 " + className}>
                    {dataLoader.error}
                </Row>
            </>
        )
    }

    return <>{ children }</>;
};

export default LoadingGateway;
