////// Packages //////////////////
import React, { FC } from "react";
import {
  Nav,
  UncontrolledDropdown,
  Media,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledTooltip,
  Input,
  Spinner
} from "reactstrap";
import { Link } from "react-router-dom";

////// Local Components //////////

////// Global Components /////////
import Flex from "./Flex";
import Avatar, { AvatarSize } from "./Avatar";

// import usePeopleList, { BasicPerson } from 'store/hooks/person/usePeopleList';

////// CommonJS Helpers //////////
import makeName from "../helpers/makeName";

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////
import { $BasicPerson, MPerson } from "store/types/models/id";

interface Props {
  avatarSize: AvatarSize;
  showMembers: number;
  people: ($BasicPerson | MPerson)[];
  isShowMemberFixed?: boolean;
  addMember?: boolean;
  isTooltip?: boolean;
  className?: string;
}

const AvatarGroup: FC<Props> = ({
  avatarSize,
  showMembers,
  people,
  isShowMemberFixed = false,
  addMember = false,
  className
}) => {
  // const peopleList = people || [];
  // const { value: avatars, ...avatarLoader } = usePeopleList(...peopleList);

  return (
    <Nav className={`avatar-group mb-0 align-items-center ${className}`}>
      {people?.slice(0, showMembers).map((person, index) => {
        return (
          <UncontrolledDropdown direction="right" nav key={index}>
            <DropdownToggle
              nav
              caret={false}
              className={index > 0 ? "ml-n1 p-0" : "p-0"}
              id={`person-${person?._id}`}
            >
              <Avatar
                src={person?.avatar?.location}
                name={makeName(person?.name)}
                size={avatarSize}
              />
            </DropdownToggle>
            <DropdownMenu className="dropdown-md px-0 py-3">
              <Media className="align-items-center px-3">
                <div className="mr-2">
                  <Avatar
                    src={person?.avatar?.location}
                    name={makeName(person?.name)}
                    size={avatarSize}
                  />
                </div>
                <Media body>
                  <h6 className="mb-0">
                    <Link
                      className="text-900 stretched-link"
                      to={`/social/profile/${person._id}`}
                    >
                      {makeName(person?.name)}
                    </Link>
                  </h6>
                  <p className="fs--2 mb-0">{person?.position}</p>
                </Media>
              </Media>
            </DropdownMenu>
          </UncontrolledDropdown>
        );
      })}
      {!isShowMemberFixed && (
        <UncontrolledDropdown nav>
          <DropdownToggle nav caret={false} className="ml-n1 p-0">
            {people && people?.length > showMembers && (
              <Avatar
                name={`+${people?.length - showMembers}`}
                size={avatarSize}
                isExact
                mediaClass="avatar-button"
                className="mr-n1"
              />
            )}
            {addMember && (
              <Avatar
                icon="plus"
                size={avatarSize}
                mediaClass="avatar-button"
              />
            )}
          </DropdownToggle>
          <DropdownMenu className="dropdown-md">
            <h6 className="dropdown-header py-0 px-3 mb-0">
              {!addMember ? "Board Members" : "Select Member"}
            </h6>
            <DropdownItem divider />
            {!addMember ? (
              <Flex className="px-3">
                {people?.map((person) => (
                  <Link
                    className="text-900 "
                    to="/pages/profile"
                    key={person?._id}
                    id={`Tooltip-${person?._id}`}
                  >
                    <Avatar
                      src={person?.avatar?.location}
                      className="mr-2"
                      size="xl"
                    />
                    <UncontrolledTooltip
                      placement="top"
                      target={`Tooltip-${person?._id}`}
                    >
                      {makeName(person?.name)}
                    </UncontrolledTooltip>
                  </Link>
                ))}
              </Flex>
            ) : (
              <>
                <form className="px-3 mb-2">
                  <Input placeholder="Search team member" bsSize="sm" />
                </form>
                <ul className="list-unstyled">
                  {people?.map(person => (
                    <li key={person?._id}>
                      <Media
                        tag="a"
                        className="align-items-center px-3 py-1 text-decoration-none"
                        href={`pages/profile`}
                      >
                        <Avatar
                          src={person?.avatar?.location}
                          name={makeName(person?.name)}
                          className="mr-2"
                          size={avatarSize}
                        />
                        <Media body>
                          <h6 className="mb-0">{makeName(person?.name)}</h6>
                        </Media>
                      </Media>
                    </li>
                  ))}
                </ul>
              </>
            )}
          </DropdownMenu>
        </UncontrolledDropdown>
      )}
    </Nav>
  );
};

export default AvatarGroup;
