import React, { FC } from 'react'

interface Props {
left: number;
width: number;
label: React.ReactNode;
className?: string;
}

const HeaderItem: FC<Props> = ({left, width, label, className}) => (
    <div
        className={['header-item', className || ''].join(' ')}
        style={{
            left,
            width
        }}
    >
        <div>{label}</div>
    </div>
);

export default HeaderItem;