class Config {
  private data: DefaultStyleConfig;

  constructor() {
    this.data = defvalues;
  }

  load = (values?: TimelineConfig) => {
    this.data = {} as DefaultStyleConfig;
    if (values) this.populate(values, defvalues, this.data);
    else this.data = defvalues;
  };

  populate(values: Record<string, any>, defvalues: Record<string, any>, final: Record<string, any>) {
    if (Object.prototype.toString.call(defvalues) !== '[object Object]') return;
    for (const key in defvalues) {
      if (!values?.[key]) {
        //if not exits
        final[key] = defvalues[key];
      } else {
        //if it does
        final[key] = values[key];
        this.populate(values[key], defvalues[key], final[key]);
      }
    }
  }

  get values() {
    return this.data;
  }
}

const config = new Config();
export default config;
