import React, { FC, useEffect, useRef, useState } from 'react';
import { DATA_CONTAINER_WIDTH, LINK_POS, MODE } from '../../Const';
import DataTask from '../../components/viewport/DataTask';
import DateHelper from '../../helpers/DateHelper';
import sizeMe from 'react-sizeme';
import DataRow from './DataRow';
import { ILink, ITask } from '../../.';

type MouseHandlerWithViewport = (event: React.MouseEvent<HTMLDivElement, MouseEvent>, viewPort: React.RefObject<HTMLDivElement>)=>void;
type TouchHandlerWithViewport = (event: React.TouchEvent<HTMLDivElement>, viewPort: React.RefObject<HTMLDivElement>)=>void;

interface Props {
  ref: React.RefObject<HTMLDivElement>
  startRow: number;
  endRow: number;
  itemHeight: number;
  data: ITask[]
  nowPosition: number;
  dayWidth: number;
  selectedItems?: (ITask | ILink)[];
  onSelectItem: (item: ITask)=>void;
  onStartCreateLink: (item: ITask, position: LINK_POS)=>void
  onFinishCreateLink: (item: ITask, position: LINK_POS)=>void
  onTaskChanging: (changeObject: {item: ITask, position: {start: number, end: number}})=>void
  onUpdateTask?: (item: ITask, update: Partial<ITask>)=>void
  onMouseUp: React.MouseEventHandler<HTMLDivElement>;
  onMouseDown: (e: React.MouseEvent<any, MouseEvent>, mode: MODE, task?: ITask)=>void;
  onMouseLeave: React.MouseEventHandler<HTMLDivElement>;
  onMouseMove: MouseHandlerWithViewport;
  onTouchStart: React.TouchEventHandler<HTMLDivElement>;
  onTouchMove: TouchHandlerWithViewport;
  onTouchEnd: React.TouchEventHandler<HTMLDivElement>;
  onTouchCancel: React.TouchEventHandler<HTMLDivElement>;
  scrollLeft: number;
  scrollTop: number;
  boundaries: any;
  height: number;
  weekends: {left: number, width: number}[];
  shouldLink?: boolean;
}

const _DataViewPort: FC<Props> = ({
  startRow,
  endRow,
  itemHeight,
  data,
  nowPosition,
  dayWidth,
  selectedItems,
  onSelectItem,
  onStartCreateLink,
  onFinishCreateLink,
  onTaskChanging,
  onUpdateTask,
  onMouseUp,
  onMouseLeave,
  onTouchEnd,
  onMouseDown,
  onMouseMove,
  onTouchMove,
  onTouchStart,
  onTouchCancel,
  scrollLeft,
  scrollTop,
  boundaries: _toFixPaint,
  height,
  weekends,
  shouldLink
}) => {
    const [childDragging, setChildDragging] = useState(false)
    const ref = useRef<HTMLDivElement>(null)
    const [linkMode, setLinkMode] = useState('link-inactive');
    useEffect(() => {
      ref.current && (ref.current.scrollLeft = 0);
    }, [ref.current])

    if (ref.current) {
      ref.current.scrollLeft = 0;
      ref.current.scrollTop = scrollTop;
    }

    function handleMouseLeave(e: React.MouseEvent<HTMLDivElement, MouseEvent>) {
      if (e.ctrlKey) {
        setLinkMode('link-inactive')
      }
      onMouseLeave(e)
    }

    function handleMouseEnter(e: React.MouseEvent<HTMLDivElement, MouseEvent>) {
      if (e.ctrlKey) {
        setLinkMode('link-active')
      }
    }

    function handleKeyDown(e: React.KeyboardEvent<HTMLDivElement> ) {
      if (e.ctrlKey) {
        setLinkMode('link-active')
      }
    }

    function handleKeyUp(e: React.KeyboardEvent<HTMLDivElement> ) {
      if (!e.ctrlKey) {
        setLinkMode('link-inactive')
      }
    }



    return (
      <div
        ref={ref}
        id="timeLinedataViewPort"
        className={"timeLine-main-data-viewPort " + (shouldLink ? 'link-active' : 'link-inactive')}
        onMouseDown={e => e.button === 0 && !childDragging && onMouseDown(e, MODE.NONE)}
        onMouseMove={e => onMouseMove(e, ref)}
        onMouseUp={onMouseUp}
        onMouseLeave={handleMouseLeave}
        onMouseEnter={handleMouseEnter}
        onKeyDown={handleKeyDown}
        onKeyUp={handleKeyUp}
        onTouchStart={e=> !childDragging && onTouchStart(e) }
        onTouchMove={e=>onTouchMove(e, ref)}
        onTouchEnd={onTouchEnd}
        onTouchCancel={onTouchCancel}
      >
        <div
          className="timeLine-main-data-container"
          style={{ height, width: DATA_CONTAINER_WIDTH, maxWidth: DATA_CONTAINER_WIDTH }}
        >
          { weekends?.map( (style, i) => <div className="weekend-marker" key={i} style={{height, zIndex: 2, left: style.left + nowPosition, width: style.width }} /> ) }
          {data?.map( (item, i) => {
            const new_position = DateHelper.dateToPixel(item.start, nowPosition, dayWidth);
            const new_width = DateHelper.dateToPixel(item.end, nowPosition, dayWidth) - new_position;
            return (
              <DataRow key={item.id} top={i * itemHeight} left={20} itemHeight={itemHeight} style={item.rowStyle}>
                <DataTask
                  item={item}
                  nowPosition={nowPosition}
                  dayWidth={dayWidth}
                  color={item.color}
                  left={new_position}
                  width={new_width}
                  height={itemHeight}
                  onChildDrag={setChildDragging}
                  isSelected={!!selectedItems?.find(i => i.id === item.id)}
                  onSelectItem={onSelectItem}
                  onStartCreateLink={onStartCreateLink}
                  onFinishCreateLink={onFinishCreateLink}
                  onTaskChanging={onTaskChanging}
                  onUpdateTask={onUpdateTask}
                />
              </DataRow>
            )
          })}
        </div>
      </div>
    );
  }

const DataViewPort = sizeMe({ monitorWidth: true, monitorHeight: true })(_DataViewPort);
export default DataViewPort;