import React, {  FC, useEffect, useState } from 'react';
import Registry from '../../helpers/registry/Registry';
import Link from './Link';
import CreateLink from './CreateLink';
import DateHelper from '../../helpers/DateHelper';
import { ILink, ITask } from '../../index';
import { LINK_POS } from 'components/gantt/Const';

interface Props {
  selectedItems?: (ILink | ITask)[];
  onSelectItem: (item: ILink | ITask)=>void;
  dayWidth: number;
  itemHeight: number;
  links: ILink[];
  data: ITask[];
  interactiveMode?: boolean;
  taskToCreate: {task: ITask, position: LINK_POS} | null;
  onFinishCreateLink: (task: ITask, position: LINK_POS)=>void;
  changingTask?: {
    item: ITask,
    position: {
      start: number,
      end: number
    }
  } | null;
  scrollLeft: number;
  nowPosition: number;
  scrollTop: number;
  onDoubleClickLink?: (e: ILink)=>void;
  registry: Registry;
}

const LinkViewPort: FC<Props> = ({
  selectedItems,
  onSelectItem,
  dayWidth,
  itemHeight,
  links,
  data,
  interactiveMode,
  taskToCreate,
  onFinishCreateLink,
  changingTask,
  scrollLeft,
  nowPosition,
  scrollTop,
  onDoubleClickLink,
  registry
}) => {
    const [cache, setCache] = useState<React.ReactNode[]>([])

    function getItemPosition (index: number, date: string | number | Date) {
      const x = DateHelper.dateToPixel(date, 0, dayWidth);
      const y = index * itemHeight + itemHeight / 2;
      return { x: x, y: y }
    }

    function renderCreateLink () {
      if (interactiveMode) {
        const record = registry.getTask(taskToCreate?.task.id);
        if (!record) return console.log('Failed to create link. Missing task in registry.')
        const position = getItemPosition(record.index, record.item.end);
        return <CreateLink 
                  start={position} 
                  record={record} 
                  onFinishCreateLink={onFinishCreateLink} 
                />
      }
    }

    useEffect(() => {
      if ( links && data ) {
        const cache = [] as React.ReactNode[];
        if (data.length == 0) return;
        links.forEach( (link, i) => {
          if (!link) return;
          const startItem = registry.getTask(link.start);
          if (!startItem) {
            cache.push(null);
            return;
          }
          const endItem = registry.getTask(link.end);
          if (!endItem) {
            cache.push(null);
            return;
          }

          const startPosition = getItemPosition(startItem.index, startItem.item.end);
          const endPosition = getItemPosition(endItem.index, endItem.item.start);
          cache.push(
            <Link
              key={link.id}
              item={link}
              start={{ x: startPosition.x, y: startPosition.y }}
              end={{ x: endPosition.x, y: endPosition.y }}
              isSelected={!!selectedItems?.find( i => i.id === link.id ) }
              onSelectItem={onSelectItem}
              onDoubleClick={onDoubleClickLink}
            />
          );
        })
        setCache(cache)
      }
    }, [selectedItems, dayWidth, links, data, itemHeight])

    useEffect(() => {

      const links = registry.getLinks(changingTask?.item.id);
      if (!links) return;

      links.forEach( (item, i) => {

        const startItem = registry.getTask(item.link.start);
        if (!startItem) return;

        const endItem = registry.getTask(item.link.end);
        if (!endItem) return;

        const startPosition = getItemPosition(startItem.index, startItem.item.end);
        if (changingTask!.item.id === item.link.start) startPosition.x = changingTask!.position.end;

        const endPosition = getItemPosition(endItem.index, endItem.item.start);
        if (changingTask!.item.id === item.link.end) endPosition.x = changingTask!.position.start;

        setCache( prev => {
          const cache = [...prev];
          cache[item.index] = (
            <Link
              key={-i - 1}
              item={item.link}
              start={{ x: startPosition.x, y: startPosition.y }}
              end={{ x: endPosition.x, y: endPosition.y }}
              isSelected={ !!selectedItems?.find( i => i.id === item.link.id ) }
              onSelectItem={ onSelectItem }
            />
          )
          return cache
        })
      })

    }, [changingTask])

    return (
      <svg x={0} y={0} width="100%" pointerEvents="none" style={{userSelect: 'none', height: '100%' }} className="timeline-link-viewport">
        <defs>
          <marker id="arrow" viewBox="0 0 10 10" refX="5" refY="5" markerWidth="9" markerHeight="9" orient="auto-start-reverse" className="gantt-connector-arrow">
            <path d="M 0 0 L 10 5 L 0 10 z" strokeLinejoin="round" />
          </marker>
        </defs>
        <g transform={`matrix(1,0,0,1,${-(0 - nowPosition)},${-scrollTop})`}>
          { cache }
          { renderCreateLink() }
        </g>
      </svg>
    );
  }

export default LinkViewPort
