import React, { Component } from 'react';
import DateHelper from '../../helpers/DateHelper';
import { MODE, LINK_POS } from '../../Const';
import Config from '../../helpers/config/Config';
import { ITask } from '../../.';


interface Props {
  left: number;
  width: number;
  height: number;
  dayWidth: number;
  onStartCreateLink: (item: ITask, position: LINK_POS)=>void
  onFinishCreateLink: (item: ITask, position: LINK_POS)=>void
  item: ITask;
  onChildDrag: (dragging: boolean)=>void;
  onUpdateTask?: (item: ITask, update: {start: Date, end: Date})=>void
  onTaskChanging: (changeObject: {item: ITask, position: {start: number, end: number}})=>void
  onSelectItem: (item: ITask)=>void;
  nowPosition: number;
  isSelected: boolean;
  color?: string;
}

interface State {
  dragging: boolean;
  mode: MODE;
  left: number;
  width: number;
}

export default class DataTask extends Component<Props, State> {
  draggingPosition?: number;

  constructor(props: Props) {
    super(props);
    this.calculateStyle = this.calculateStyle.bind(this);
    this.state = { dragging: false, left: this.props.left, width: this.props.width, mode: MODE.NONE };
  }

  onCreateLinkMouseDown = ( e: React.MouseEvent<HTMLDivElement, MouseEvent>, position: LINK_POS ) => {
    if ( e.button === 0 ) {
      e.stopPropagation();
      this.props.onStartCreateLink(this.props.item, position);
    }
  };
  onCreateLinkMouseUp = (e: React.MouseEvent<HTMLDivElement, MouseEvent>, position: LINK_POS) => {
    e.stopPropagation();
    this.props.onFinishCreateLink(this.props.item, position);
  };
  onCreateLinkTouchStart = (e: React.TouchEvent<HTMLDivElement>, position: LINK_POS) => {
    if (e.touches.length === 2) {
      e.stopPropagation();
      this.props.onStartCreateLink(this.props.item, position);
    }
  };
  onCreateLinkTouchEnd = (e: React.TouchEvent<HTMLDivElement>, position: LINK_POS) => {
    e.stopPropagation();
    this.props.onFinishCreateLink(this.props.item, position);
  };

  componentDidUpdate(props: Partial<Props>, state: State) {
    if (this.state.dragging && !state.dragging) {
      document.addEventListener('mousemove', this.doMouseMove);
      document.addEventListener('mouseup', this.doMouseUp);
      document.addEventListener('touchmove', this.doTouchMove);
      document.addEventListener('touchend', this.doTouchEnd);
    } else if (!this.state.dragging && state.dragging) {
      document.removeEventListener('mousemove', this.doMouseMove);
      document.removeEventListener('mouseup', this.doMouseUp);
      document.removeEventListener('touchmove', this.doTouchMove);
      document.removeEventListener('touchend', this.doTouchEnd);
    }
  }

  dragStart(x: number, mode: MODE) {
    this.props.onChildDrag(true);
    this.draggingPosition = x;
    this.setState({
      dragging: true,
      mode: mode,
      left: this.props.left,
      width: this.props.width
    });
  }

  dragProcess(x: number) {
    if (!this.draggingPosition) return
    const delta = this.draggingPosition - x;
    let newLeft = this.state.left!;
    let newWidth = this.state.width!;

    switch (this.state.mode) {
      case MODE.MOVE:
        newLeft = this.state.left! - delta;
        break;
      case MODE.MOVE_RESIZE_LEFT:
        newLeft = this.state.left! - delta;
        newWidth = this.state.width! + delta;
        break;
      case MODE.MOVE_RESIZE_RIGHT:
        newWidth = this.state.width! - delta;
        break;
    }
    //the coordinates need to be global
    const changeObj = {
      item: this.props.item,
      position: { start: newLeft - this.props.nowPosition, end: newLeft + newWidth - this.props.nowPosition }
    };
    this.props.onTaskChanging(changeObj);
    this.setState({ left: newLeft, width: newWidth });
    this.draggingPosition = x;
  }
  dragEnd() {
    this.props.onChildDrag(false);
    const new_start_date = DateHelper.pixelToDate(this.state.left!, this.props.nowPosition, this.props.dayWidth);
    const new_end_date = DateHelper.pixelToDate(this.state.left! + this.state.width!, this.props.nowPosition, this.props.dayWidth);
    this.props.onUpdateTask && this.props.onUpdateTask(this.props.item, { start: new_start_date, end: new_end_date });
    this.setState({ dragging: false, mode: MODE.NONE });
  }

  doMouseDown = (e: MouseEvent | React.MouseEvent<HTMLDivElement>, mode: MODE) => {
    if (!this.props.onUpdateTask) return;
    if (e.button === 0) {
      e.stopPropagation();
      this.dragStart(e.clientX, mode);
    }
  };

  doMouseMove = (e: MouseEvent | React.MouseEvent<HTMLDivElement>) => {
    if (this.state.dragging) {
      e.stopPropagation();
      this.dragProcess(e.clientX);
    }
  };

  doMouseUp = () => {
    this.dragEnd();
  };

  doTouchStart = (e: TouchEvent | React.TouchEvent<HTMLDivElement>, mode: MODE) => {
    if (!this.props.onUpdateTask) return;
    e.stopPropagation();
    this.dragStart(e.touches[0].clientX, mode);
  };
  doTouchMove = (e: TouchEvent | React.TouchEvent<HTMLDivElement>) => {
    if (this.state.dragging) {
      e.stopPropagation();
      this.dragProcess(e.changedTouches[0].clientX);
    }
  };
  doTouchEnd = (e: TouchEvent | React.TouchEvent<HTMLDivElement>) => {
    this.dragEnd();
  };

  calculateStyle() {
    const configStyle = this.props.isSelected ? Config.values.dataViewPort.task.selectedStyle : Config.values.dataViewPort.task.style;
    const backgroundColor = this.props.color || configStyle.backgroundColor;
    const borderColor = this.props.item.borderColor || configStyle.borderColor;

    if (this.state.dragging) {
      return {
        ...configStyle,
        borderColor,
        backgroundColor: backgroundColor,
        left: this.state.left,
        width: this.state.width,
        height: this.props.height - 5,
        top: 2
      };
    } else {
      return { 
        ...configStyle,
        borderColor,
        backgroundColor, 
        left: this.props.left, 
        width: this.props.width, 
        height: this.props.height - 5, 
        top: 2 
      };
    }
  }

  render() {
    const style = this.calculateStyle();
    return (
      <div
        className={"gantt-task rounded" + (this.props.isSelected ? " selected" : '')}
        onMouseDown={(e) => this.doMouseDown(e, MODE.MOVE)}
        onTouchStart={(e) => this.doTouchStart(e, MODE.MOVE)}
        onClick={(e) => {
          this.props.onSelectItem(this.props.item);
        }}
        style={style}
      >
        <div
          className="timeLine-main-data-task-side"
          style={{ top: 0, left: -8, height: style.height }}
          onMouseDown={(e) => this.doMouseDown(e, MODE.MOVE_RESIZE_LEFT)}
          onTouchStart={(e) => this.doTouchStart(e, MODE.MOVE_RESIZE_LEFT)}
        >
          <div
            className="timeLine-main-data-task-side-linker"
            onMouseUp={(e) => this.onCreateLinkMouseUp(e, LINK_POS.LEFT)}
            onTouchEnd={(e) => this.onCreateLinkTouchEnd(e, LINK_POS.LEFT)}
          />
        </div>
        <div style={{ overflow: 'hidden' }}>{this.props.item.label}</div>
        <div
          className="timeLine-main-data-task-side"
          style={{ top: 0, left: style.width - 8, height: style.height }}
          onMouseDown={(e) => this.doMouseDown(e, MODE.MOVE_RESIZE_RIGHT)}
          onTouchStart={(e) => this.doTouchStart(e, MODE.MOVE_RESIZE_RIGHT)}
        >
          <div
            className="timeLine-main-data-task-side-linker"
            onMouseDown={(e) => this.onCreateLinkMouseDown(e, LINK_POS.RIGHT)}
            onTouchStart={(e) => this.onCreateLinkTouchStart(e, LINK_POS.RIGHT)}
          />
        </div>
      </div>
    );
  }
}
