////// Packages //////////////////
import LoadingGateway from 'components/LoadingGateway';
import React, { FC, useEffect, useRef, useState } from 'react'
import { batch, useDispatch, useSelector } from 'react-redux'
import useMyAssignments from 'store/hooks/workflow/useMyAssignments';
import { MPerson } from 'store/types/models/id'
import KanbanColumn, { KanbanConfiguredAssignment, KanbanItem } from './KanbanColumn';
import KanbanHeader from './KanbanHeader';
import { DragDropContext, DropResult, ResponderProvided } from 'react-beautiful-dnd';

////// Local Components //////////

////// Global Components /////////
import Flex from 'components/Flex';
import is from 'is_js';
import useSetting from 'store/hooks/settings/useSettings';
import useKanbanCategories from 'store/hooks/management/useKanbanCategories';
import useToDos from 'store/hooks/management/useToDos';
import { MToDo } from 'store/types/models/management';

////// CommonJS Helpers //////////

////// Custom Hooks //////////////

////// Thunks ////////////////////
import { toDos as toDosController } from 'store/thunks/management';
import { tasks as tasksController } from 'store/thunks/workflow/tasks';
import { Dispatch } from 'redux';
import { toast } from 'react-toastify';
import useAction from 'store/hooks/useAction';

////// Types /////////////////////

interface Props {
    
}

function compareArrays(array: KanbanItem[], category: string, dispatch: Dispatch<any>, personId: MPerson['_id']) {
    const movedAssignments = [] as {taskId: string, assignmentId: string, sortIndex: number, category: string;}[]
    const movedToDos = [] as {toDoId: string, sortIndex: number, category: string}[]
    for ( let i = 0; i < array.length; i++ ) {
        const item = array[i]
        if (!item) continue;
        switch(item.type) {
            case "assignment": {
                const config = item.assignees.find( a => a.personId === personId)!
                if (config.sortIndex !== i || config.category !== category) {
                    dispatch( 
                        tasksController.upsert(
                            {
                                assignments: [{
                                    _id: item._id, 
                                    assignees: [{
                                        _id: config._id,
                                        sortIndex: i
                                    }]
                                }]
                            },
                            item.task._id
                        )
                    )
                    movedAssignments.push({
                        taskId: item.task._id, 
                        assignmentId: item._id,
                        sortIndex: i,
                        category: category
                    })
                }
                break;
            }
            case "todo":
                if (item.kanban.sortIndex !== i || item.kanban.category !== category) {
                    dispatch( 
                        toDosController.upsert(
                            {
                                kanban: {
                                    sortIndex: i
                                }
                            },
                            item._id
                        )
                    )
                    movedToDos.push({
                        toDoId: item._id,
                        sortIndex: i,
                        category: category
                    })
                }
                break;
        }
    }
    return {movedAssignments, movedToDos}
}

function handleResortOut(
    draggableId: string, 
    category: string, 
    index: number, 
    dispatch: Dispatch<any>, 
    categories: Map<string, KanbanItem[]>, 
    personId: MPerson['_id']
) {
    const [_d, type, id] = draggableId.split('-')

    const arr = categories.get(category)!
                            .slice()
                            .sort( (a, b) => a.kanban.sortIndex > b.kanban.sortIndex ? 1 : -1)

    const splicedIndex = arr.findIndex( item => item.type === type && item._id === id)
    if (splicedIndex === -1) {
        toast.error('there was a problem re-sorting out')
        return {movedAssignments: [], movedToDos: []};
    }
    arr.splice(splicedIndex, 1);
    categories.set(category, arr.map( (item, i) => ({...item, kanban: {...item.kanban, sortIndex: i}})));
    
    return compareArrays(arr, category, dispatch, personId)
}

function handleResortIn(
    draggableId: string, 
    fromCategory: string,
    toCategory: string, 
    index: number, 
    dispatch: Dispatch<any>, 
    categories: Map<string, KanbanItem[]>, 
    personId: MPerson['_id']
) {
    const [_d, type, id] = draggableId.split('-')
    const draggedItem = categories.get(fromCategory)!.find( _i => _i._id === id && _i.type === type);
    if (!draggedItem) {
        toast.error('there was a problem re-sorting in')
        return {movedAssignments: [], movedToDos: []};
    }
    const arr = categories.get(toCategory)!
                            .slice()
                            .sort( (a, b) => a.kanban.sortIndex > b.kanban.sortIndex ? 1 : -1)

    arr.splice(index, 0, draggedItem);
    categories.set(toCategory, arr.map( (item, i) => ({...item, kanban: {...item.kanban, sortIndex: i}})));
    return compareArrays(arr, toCategory, dispatch, personId)
}

function handleResortSame(
        draggableId: string, 
        category: string,
        index: number, 
        dispatch: Dispatch<any>, 
        categories: Map<string, KanbanItem[]>, 
        personId: MPerson['_id']
    ) {
    const arr = categories.get(category)!
                            .slice()
                            .sort( (a, b) => a.kanban.sortIndex > b.kanban.sortIndex ? 1 : -1)

    const [_d, type, id] = draggableId.split('-')

    const spliced = arr.splice( arr.findIndex( item => item._id === id && item.type === type ), 1 )
    if (!spliced.length) {
        toast.error('there was a problem re-sorting')
        return {movedAssignments: [], movedToDos: []};
    }

    arr.splice(index, 0, ...spliced)
    categories.set(category, arr.map( (item, i) => ({...item, kanban: {...item.kanban, sortIndex: i}})));
    return compareArrays(arr, category, dispatch, personId)
}

const Assignments: FC<Props> = () => {
    const personId = useSelector(state => state.app.login.personId);
    const [delimiter, setDelimiter] = useState<MToDo["_id"]>();
    const [trappedCategories, setTrappedCategories] = useState<Map<string, KanbanItem[]>>();
    const {value: userCategories, ...userCategoriesLoader} = useKanbanCategories();

    const { value: assignments, ...assignmentsLoader } = useMyAssignments();
    const { value: toDos, ...toDosLoader } = useToDos(delimiter);

    const reorderAssignmentsAction = useAction( tasksController.reorderAssignments )
    const reorderToDosAction = useAction( toDosController.reorderToDos )

    const containerRef = useRef<HTMLDivElement>(null);
    const dispatch = useDispatch();

    const _categories = new Map<string, KanbanItem[]>(userCategories?.map( cat => [ cat, [] ] ) )

    // Detect device
    useEffect(() => {
        if (is.ipad()) {
            containerRef.current?.classList.add('ipad');
        }
        if (is.mobile()) {
            containerRef.current?.classList.add('mobile');
            if (is.safari()) {
                containerRef.current?.classList.add('safari');
            }
            if (is.chrome()) {
                containerRef.current?.classList.add('chrome');
            }
        }
    }, []);


    assignments?.forEach( a => {
        const config = a.assignees.find( e => e.personId === personId );
        const category = a.complete ? 'Complete' : (config?.category || 'Uncategorized');
        const aWithConfig = {
            ...a,
            type: 'assignment' as const,
            kanban: {
                category,
                sortIndex: config?.sortIndex || 0
            }
        }
        if ( _categories.has(category) ) {
            _categories.get(category)!.push(aWithConfig)
        } else {
            _categories.set(category, [aWithConfig])
        }
    })

    toDos?.forEach( t => {
        const category = t.complete ? 'Complete' : (t.kanban.category || 'Uncategorized');
        const tWithConfig = {
            ...t,
            type: 'todo' as const,
            kanban: {
                category,
                sortIndex: t.kanban.sortIndex || 0,
                color: t.kanban.color
            }
        }
        if ( _categories.has(category) ) {
            _categories.get(category)!.push(tWithConfig)
        } else {
            _categories.set(category, [tWithConfig])
        }
    })

    const categories = trappedCategories || _categories;

    async function reorderAssignments(items: {assignmentId: string, taskId: string, sortIndex: number, category: string}[]) {
        const data = await reorderAssignmentsAction.post(items)
        if (!data?.success) toast.error('There was a problem re-sorting the assignments. Please refresh the page and try again.')
    }

    async function reorderToDos(items: {toDoId: string, sortIndex: number, category: string}[]) {
        const data = await reorderToDosAction.post(items)
        if (!data?.success) toast.error('There was a problem re-sorting the todos. Please refresh the page and try again.')
    }

    const getBatchedActions = (
        result: DropResult, 
        newCat: string | undefined, 
        dispatch: Dispatch<any>, 
        categories: Map<string, KanbanItem[]>, 
        personId: MPerson['_id']
    ) => async () => {
        const categoriesMutableCopy = new Map(categories);
        if (newCat !== undefined) {
            const {movedAssignments: a2, movedToDos: t2} = handleResortIn(
                result.draggableId, 
                result.source.droppableId, 
                newCat, 
                result.destination!.index, 
                dispatch, 
                categoriesMutableCopy, 
                personId!
            )
            const {movedAssignments: a1, movedToDos: t1} = handleResortOut(
                result.draggableId,
                result.source.droppableId,
                result.source.index,
                dispatch,
                categoriesMutableCopy,
                personId!
            )
            const assignmentsToSend = [...a1, ...a2];
            const toDosToSend = [...t1, ...t2];
            setTrappedCategories(categoriesMutableCopy)
            await Promise.all([
                assignmentsToSend.length > 0 ? reorderAssignments(assignmentsToSend) : Promise.resolve(),
                toDosToSend.length > 0 ? reorderToDos(toDosToSend) : Promise.resolve()
            ])
            setTrappedCategories(undefined)
        } else {
            const {movedAssignments, movedToDos} = handleResortSame(
                result.draggableId, 
                result.source.droppableId, 
                result.destination!.index,
                dispatch,
                categoriesMutableCopy,
                personId!
            )
            setTrappedCategories(categoriesMutableCopy)
            await Promise.all([
                movedAssignments.length > 0 ? reorderAssignments(movedAssignments) : Promise.resolve(),
                movedToDos.length > 0 ? reorderToDos(movedToDos) : Promise.resolve()
            ])
            setTrappedCategories(undefined)
        }
    }

    function onDragEnd(result: DropResult) {
        let newCat: string | undefined = undefined;
        let newIndex: number | undefined = undefined;

        if (result.reason === 'DROP' && result.destination) {
            if (result.destination.droppableId !== result.source.droppableId ) {
                newCat = result.destination.droppableId
                newIndex = result.destination.index
            } else {
                if (result.destination.index !== result.source.index ) {
                    newIndex = result.destination.index
                }
            }
        }
        if (newIndex !== undefined) {
            batch(getBatchedActions(result, newCat, dispatch, categories, personId!))
        }
    }

    const strCategories = Array.from(categories.keys()) || []
    return (
        <LoadingGateway dataLoader={assignmentsLoader}>
            <KanbanHeader categories={strCategories} />
            <DragDropContext onDragEnd={onDragEnd}>
                <div className="kanban-container scrollbar" ref={containerRef}>
                    { 
                        Array.from(categories.keys())
                            .sort((a, b) => {
                                if (a === 'Complete') return 1
                                if (b === 'Complete') return -1
                                return 0
                            })
                            .map( (c, i) => <KanbanColumn 
                                                key={i} 
                                                category={c} 
                                                index={i} 
                                                items={categories.get(c) || []} 
                                                categories={strCategories} 
                                                fakeDragEvent={onDragEnd}
                                            /> ) 
                    }
                    {/* <AddAnotherList /> */}
                </div>
            </DragDropContext>
            <Flex>
                
            </Flex>
        </LoadingGateway>
    )
}

export default Assignments