////// Packages //////////////////
import React, { FC } from 'react'
import classNames from 'classnames';

////// Local Components //////////

////// Global Components /////////

////// CommonJS Helpers //////////
import { FalconColors } from './falconTypes';

////// Custom Hooks //////////////

////// Thunks ////////////////////

////// Types /////////////////////

interface Props extends Omit<React.HTMLProps<HTMLDivElement>, 'size'> {
    size: 'sm' | 'md' | 'lg';
    color: FalconColors;
    spinnerStyle?: React.CSSProperties;
}

const BounceSpinner: FC<Props> = ({size, color, spinnerStyle, ...rest}) => {
    const {className, ...divProps} = rest;
    
    return (
        <div 
            className={classNames("bounce-spinner", {
                "bounce-spinner-sm": size === "sm",
                "bounce-spinner-md": size === "md",
                "bounce-spinner-lg": size === "lg"
            }, className)} 
            {...divProps}
        >
            <div style={spinnerStyle} className={classNames("bounce1", "bg-" + color)}></div>
            <div style={spinnerStyle} className={classNames("bounce2", "bg-" + color)}></div>
            <div style={spinnerStyle} className={classNames("bounce3", "bg-" + color)}></div>
        </div>

    )
}

export default BounceSpinner