////// Packages /////////////////////
import React, { FC } from 'react'

////// Global Components ////////////

////// Local Components /////////////
import ContentEditable from '../common/ContentEditable';

////// Helpers /////////////////////
import Config from '../../helpers/config/Config';

////// Hooks ///////////////////////

////// Thunks //////////////////////

////// Types ///////////////////////
import { ITask } from 'components/gantt';


interface Props {
    item: ITask;
    top: number;
    itemHeight: string | number;
    onSelectItem: (item: ITask)=>void;
    index: number;
    label: string;
    onUpdateTask?: (item: ITask, config: Record<string, any>)=>void;
    nonEditable?: boolean;
    isSelected?: boolean
}

const TaskRow: FC<Props> = ({item, top, itemHeight, onSelectItem, onUpdateTask, index, label, nonEditable, isSelected}) => (
    <div
        className={"timeLine-side-task-row " + (isSelected ? 'selected' : '')}
        style={{
            ...Config.values.taskList.task.style,
            ...item.rowStyle,
            top,
            height: itemHeight
        }}
        onClick={()=>onSelectItem(item)}
    >
        {item.tableContent ? item.tableContent(item, itemHeight) : <div tabIndex={index} style={{ width: '100%' }}> { label } </div>}
    </div>
);
  
  export default TaskRow
