import { DataListItem, DataRegistry, ILink, LinkRegistry } from "../../.";

export default class Registry {
    link: LinkRegistry;
    data: DataRegistry;

    constructor(){
        this.data={}
        this.link={}
    }

    registerData(list: DataListItem[]){
        if (!list)
            return;
        this.data={}
        for (let i=0;i<list.length;i++){
            this.data[list[i].id]={item:list[i],index:i};
        }
    }

    registerLinks(list: ILink[]){
        if(!list)
            return
        this.link={}

        list.forEach((link, index) => {
            this.link[ link.start ] = this.link[ link.start ] || []
            this.link[ link.start ].push( { link, index } )

            this.link[ link.end ] = this.link[ link.end ] || []
            this.link[ link.end ].push( { link, index } )
        })
    }

    getTask(id?: string | number){
        if (!id) return;
        return this.data[id]
    }

    getLinks(id?: string | number){
        if (!id) return
        return this.link[id]
    }

}