////// Packages //////////////////
import React, { FC, useState } from 'react';
import { Draggable, DropResult } from 'react-beautiful-dnd';
import {
  Card,
  CardBody,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { toast } from 'react-toastify';
import confirm from 'reactstrap-confirm';
import dayjs from 'dayjs';

////// Local Components //////////
import EditToDo from './EditToDo';

////// Global Components /////////

////// CommonJS Helpers //////////
import { getItemStyle } from './helpers';

////// Custom Hooks //////////////
import useAction from 'store/hooks/useAction';

////// Thunks ////////////////////
import { deleteToDo } from 'store/thunks/management';

////// Types /////////////////////
import { AssignmentWithTaskInfo, MAssignment } from 'store/types/models/workflow';
import { KanbanTodo } from './KanbanColumn';






export interface KanbanAssignment extends AssignmentWithTaskInfo {
  type: 'assignment'
}

interface Props {
    toDo: KanbanTodo;
    category: string;
    index: number;
    lastIndex: number;
    fakeDragEvent: (result: Partial<DropResult>)=>void;
    categories: string[]
}

const ToDoCard: FC<Props> = ({ toDo, category, index, lastIndex, categories, fakeDragEvent }) => {
    const [isOpen, setIsOpen] = useState(false);

    const deleteAction = useAction( deleteToDo );



    async function handleDelete() {
        const confirmed = await confirm({
            title: 'Delete To-Do?',
            message: 'Are you sure you want to delete this To-Do?',
            confirmColor: 'danger',
            confirmText: 'Delete',
            cancelColor: 'secondary',
            cancelText: 'Cancel'            
        })
        if (!confirmed) return
        const data = await deleteAction.post(toDo._id)
        if (data?.success) {
            toast.success('Deleted to-do');
        } else {
            toast.error(data?.error || "Something went wrong");
        }
    }

    const draggableId = `draggable-todo-${toDo._id}`;

    return (
        <Draggable draggableId={`draggable-todo-${toDo._id}`} index={index}>
        {(provided, snapshot) => (
            <div
            className="kanban-item"
            ref={provided.innerRef}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
            >
            <Card
                className="kanban-item-card"
                style={{...getItemStyle(snapshot.isDragging), backgroundColor: toDo.kanban.color || 'rgb(255, 255, 200)', color: 'rgb(50, 50, 50)'}}
            >
                <CardBody>
                    { 
                        toDo.description
                            .split('\n')
                            .map((str, i) => (
                                <p className="mb-1 fs--1 text-sans-serif" key={i}>
                                    {str}
                                </p>
                            )) 
                    }
                
                <div className="kanban-item-footer">
                    <div className="text-500">
                    {toDo.dueDate && dayjs(toDo.dueDate).format("MM/DD/YYYY")}
                    </div>
                    <UncontrolledDropdown
                    className="text-sans-serif"
                    onClick={e => {
                        e.stopPropagation();
                    }}
                    >
                    <DropdownToggle color="falcon-default" size="sm" className="py-0 px-2">
                        <FontAwesomeIcon icon="ellipsis-h" />
                    </DropdownToggle>
                    <DropdownMenu right className="py-0">
                        {index > 0 && 
                        <DropdownItem
                            onClick={()=>fakeDragEvent(
                            {
                                reason: 'DROP', 
                                draggableId,
                                destination: {
                                droppableId: category, 
                                index: index - 1
                                }, 
                                source: {
                                droppableId: category, 
                                index: index
                                }
                            }
                            )}
                        >Move Up</DropdownItem>
                        }
                        {index < lastIndex && 
                        <DropdownItem
                            onClick={()=>fakeDragEvent(
                            {
                                reason: 'DROP', 
                                draggableId,
                                destination: {
                                droppableId: category, 
                                index: index + 1
                                }, 
                                source: {
                                droppableId: category, 
                                index: index
                                }
                            }
                            )}
                        >
                            Move Down
                        </DropdownItem>
                        }
                        <DropdownItem divider />
                        {categories.filter(c => c !== category).map( c => (
                        <DropdownItem
                            onClick={()=>fakeDragEvent(
                            {
                                reason: 'DROP',
                                draggableId,
                                destination: {
                                droppableId: c, 
                                index: 0
                                }, 
                                source: {
                                droppableId: category, 
                                index: index
                                }
                            }
                            )}
                            key={c}
                        >
                            {c}
                        </DropdownItem>
                        ))}
                        <DropdownItem divider />
                        <DropdownItem 
                            className="text-primary"
                            onClick={()=>setIsOpen(true)}
                        >
                            Edit
                        </DropdownItem>
                        <DropdownItem 
                            className="text-danger"
                            onClick={handleDelete}
                        >
                        Delete
                        </DropdownItem>
                    </DropdownMenu>
                    </UncontrolledDropdown>
                </div>
                
                </CardBody>
            </Card>
            {isOpen && <EditToDo toDo={toDo} setIsOpen={setIsOpen} />}
            </div>
        )}
        </Draggable>
    );
};

export default ToDoCard;
