import React, { FC } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import { RouteComponentProps } from 'react-router'
import UnderConstruction from 'layouts/errors/UnderConstruction';
import Assignments from 'layouts/management/assignments/Assignments';
import TimelineTest from 'layouts/management/timelines/TimelineTest';

interface Props extends RouteComponentProps {

}

const ManagmentRoutes: FC<Props> = ({ match: { url } }) => (
  <Switch>
    <Route path={`${url}/assignments`} component={Assignments} />
    <Route path={`${url}/teams`} component={UnderConstruction} />
    <Route path={`${url}/workload`} exact component={UnderConstruction} />
    <Route path={`${url}/approvals`} exact component={UnderConstruction} /> 
    <Route path={`${url}/timelines`} exact component={TimelineTest} /> 

    {/*Redirect*/}
    <Redirect to="/errors/404" />
  </Switch>
);

export default ManagmentRoutes;
