import React, {FC} from 'react';
import classNames from 'classnames';
import { Button, ButtonProps } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Transform, IconProp } from '@fortawesome/fontawesome-svg-core'


export interface Props extends ButtonProps {
  iconAlign?: 'right' | 'left',
  iconClassName?: string;
  transform?: string | Transform ;
  icon: IconProp;
}

const ButtonIcon: FC<Props> = ({ icon, iconAlign='left', iconClassName, transform, children, ...rest }) => (
  <Button {...rest}>
    {iconAlign === 'right' && children}
    <FontAwesomeIcon
      icon={icon}
      className={classNames(iconClassName, {
        'mr-1': children && iconAlign === 'left',
        'ml-1': children && iconAlign === 'right'
      })}
      transform={transform}
    />
    {iconAlign === 'left' && children}
  </Button>
);

export default ButtonIcon;
