import { DataRegistryItem, ITask } from 'components/gantt';
import { LINK_POS } from 'components/gantt/Const';
import React, { Component } from 'react';
import Link from './Link';

interface Props {
  start: {x: number, y: number};
  record: DataRegistryItem;
  onFinishCreateLink: (task: ITask, position: LINK_POS)=>void;
}

interface State {
  x: number,
  y: number
}

export default class CreateLink extends Component<Props, State> {
  init: boolean;
  lastX: number;
  lastY: number;

  constructor(props: Props) {
    super(props);
    this.state = { x: this.props.start.x, y: this.props.start.y };
    this.init = false;
    this.lastX = -1;
    this.lastY = -1;
  }

  componentDidMount() {
    document.addEventListener('mousemove', this.doMouseMove);
    document.addEventListener('mouseup', this.doMouseUp);
  }
  
  componentWillUnmount() {
    document.removeEventListener('mousemove', this.doMouseMove);
    document.removeEventListener('mouseup', this.doMouseUp);
  }

  doMouseMove = (e: MouseEvent) => {
    if (!this.init) {
      this.lastX = e.clientX;
      this.lastY = e.clientY;
      this.init = true;
    }
    const newX = this.state.x + (e.clientX - this.lastX);
    const newY = this.state.y + (e.clientY - this.lastY);
    this.lastX = e.clientX;
    this.lastY = e.clientY;
    this.setState({ x: newX, y: newY });
  };

  doMouseUp = (e: MouseEvent) => {
    this.props.onFinishCreateLink(this.props.record.item, LINK_POS.LEFT/* {start: {...this.props.start}, end: {...this.state}} */);
  };

  render() {
    return <Link key={-1} start={{...this.props.start}} end={{...this.state}} />;
  }
}
