import { ILink, IPosition } from '../../index';
import React, { Component, FC } from 'react';
import Config from '../../helpers/config/Config';

const SSHAPE_SIDE_WIDTH = 20;

interface Props {
  start: {x: number, y: number};
  end: {x: number, y: number};
  onSelectItem?: (item: ILink)=>void;
  item?: ILink;
  isSelected?: boolean;
  onDoubleClick?: (link: ILink)=>void
}

function calcNormCoordinates(start: IPosition, end: IPosition)  {
  const middle = start.x + (end.x - start.x) / 2;
  const cpt1 = { x: middle, y: start.y };
  const cpt2 = { x: middle, y: end.y };
  return { cpt1, cpt2 };
}

function calcSCoordinates (start: IPosition, end: IPosition) {
  const cpt1 = { x: start.x + SSHAPE_SIDE_WIDTH, y: start.y };
  const halfY = (end.y - start.y) / 2;
  const cpt2 = { x: cpt1.x, y: cpt1.y + halfY };
  const cpt3 = { x: end.x - SSHAPE_SIDE_WIDTH, y: cpt2.y };
  const cpt4 = { x: cpt3.x, y: cpt3.y + halfY };
  return { cpt1, cpt2, cpt3, cpt4 };
}

function getPath (start: IPosition, end: IPosition) {
  if (start.x > end.x) {
    const coordinates = calcSCoordinates(start, end);
    return "M" + [
      start.x,
      start.y,
      coordinates.cpt1.x,
      coordinates.cpt1.y,
      coordinates.cpt2.x,
      coordinates.cpt2.y,
      coordinates.cpt3.x,
      coordinates.cpt3.y,
      coordinates.cpt4.x,
      coordinates.cpt4.y,
      end.x,
      end.y
    ].join(' ');
  } else {
    const coordinates = calcNormCoordinates(start, end);
    return "M" + [
      start.x,
      start.y,
      coordinates.cpt1.x,
      coordinates.cpt1.y,
      coordinates.cpt2.x,
      coordinates.cpt2.y,
      end.x,
      end.y
    ].join(' ');
  }
}
const Link: FC<Props> = ({start, end, onSelectItem, item, isSelected, onDoubleClick}) => {
  const handleSelect = (e: React.MouseEvent<SVGPathElement, MouseEvent>) => {
    if (onSelectItem && item) onSelectItem(item);
  };

  const handleDoubleClick = (e: React.MouseEvent<SVGPathElement, MouseEvent>) => {
    if (onDoubleClick && item) onDoubleClick(item);
  }

  const path = getPath(start, end)
    return (
      <g className="timeline-link">
        <path
          className={"gantt-connector-outline" + (isSelected ? ' selected' : '')}
          pointerEvents="stroke"
          onMouseDown={handleSelect}
          onDoubleClick={handleDoubleClick}
          d={path}
          strokeLinejoin="round"
          fill="transparent"
          cursor="pointer"
        />

        <path
          className={"gantt-connector" + (isSelected ? ' selected' : '')}
          pointerEvents="stroke"
          onMouseDown={handleSelect}
          onDoubleClick={handleDoubleClick}
          d={path}
          strokeLinejoin="round"
          fill="transparent"
          cursor="pointer"
          markerEnd="url(#arrow)"
        />

        {/* <circle cx={this.props.start.x} 
                    cy={this.props.start.y} r="3" fill='white'  
                    stroke={pathColor} strokeWidth="1" />     */}
      </g>
    );
  }

export default Link;
