import NewtonObject from "./NewtonObject";
import { Attachable, Coordinates3D } from "./Attachable";
import { coordsToMagnitude } from "./Vector";

export default class RubberBand implements Attachable {
    private _fulcrum = {
        x: 0,
        y: 0,
        z: 0
    }

    public get fulcrum() {return {...this._fulcrum}}

    private _attachment?: NewtonObject;

    public springK = 0;

    constructor( fulcrum: Coordinates3D, springK: number ) {
        this._fulcrum = Object.assign({}, this._fulcrum, fulcrum);
        this.springK = springK
    }

    attachObject( object: NewtonObject) {
        this._attachment = object;
    }

    getObject() {
        return this._attachment;
    }

    detatchObject() {
        this._attachment = undefined;
    }

    getForceVector() {
        if (this._attachment) {
            const att = this._attachment;
            const distanceVector = {
                                    x: this._fulcrum.x - att.centroid.x, 
                                    y: this._fulcrum.y - att.centroid.y, 
                                    z: this._fulcrum.z - att.centroid.z
                                }
            const distance = coordsToMagnitude(distanceVector);
            const directionVector = distance ? {
                x: distanceVector.x / distance,
                y: distanceVector.y / distance,
                z: distanceVector.z / distance 
            } : {x: 0, y: 0, z: 0};
            const force = distance * this.springK;
            return {
                x: force * directionVector.x,
                y: force * directionVector.y,
                z: force * directionVector.z
            }
        }
    }

    setFulcrum(fulcrum: {x: number, y: number, z: number}) {
        this._fulcrum = fulcrum
        this._attachment?.forceChange()
    }
}