import React, { FC, useState } from 'react';
import { UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Modal, ModalHeader, ModalFooter, ModalBody} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { KanbanItem } from './KanbanColumn';
import Flex from 'components/Flex';
import SubmitButton from 'compositions/SubmitButton';
import { toast } from 'react-toastify';
import { setKanbanCategories } from 'store/thunks/settings';
import useAction from 'store/hooks/useAction';

interface Props {
  category: string;
  items: KanbanItem[];
  categories: string[]
}

const KanbanColumnHeader: FC<Props> = ({ category, items, categories }) => {
  const [isOpenRemoveModal, setIsOpenRemoveModal] = useState(false);
  const setCategoriesAction = useAction( setKanbanCategories )

  async function handleRemoveColumn() {
    if (items.length) {
      toast.error('Cannot remove category with items. Please remove all items from this category before removing.')
      return;
    }
    const data = await setCategoriesAction.post(categories.filter( c => c !== category ))
    if (data?.success) {
      toast.success('Removed')
    } else {
      toast.error(data?.error || 'Something went wrong')
    }
  }

  async function handleMove(change: number) {
    const newCategories = [...categories];
    newCategories.sort( (a, b) => (a === 'Complete') 
                                    ? 1 
                                    : (b === 'Complete') 
                                      ? -1 
                                      : 0 
                      )
    const thisIndex = newCategories.findIndex( c => c === category );
    const newIndex = thisIndex + change
    if (newIndex < 0 || newIndex > categories.length - 2) {
      toast.error("Couldn't move any more")
      return;
    }
    newCategories[thisIndex] = newCategories[thisIndex + change];
    newCategories[thisIndex + change] = category;
    const data = await setCategoriesAction.post( newCategories );
    if (!data?.success) toast.error(data?.error || 'Failed to sort');
  }

  return (
    <div className="kanban-column-header">
      <h5 className="text-serif fs-0 mb-0">
        {category} <span className="text-500">({items.length})</span>
      </h5>
      <Modal isOpen={isOpenRemoveModal} toggle={()=>setIsOpenRemoveModal(false)}>
        <ModalHeader>
          Remove Category
        </ModalHeader>
        <ModalBody>
          {items.length 
            ? 'Please remove all items from the category first.'
            : 'Do you want to remove this category?'
          }
        </ModalBody>
        <ModalFooter tag={Flex} justify="end">
          <SubmitButton
            onClick={handleRemoveColumn}
            submitting={setCategoriesAction.loading}
            text="Remove"
            color="danger"
            disabled={!!items.length}
          />
        </ModalFooter>
      </Modal>
      <UncontrolledDropdown className="text-sans-serif btn-reveal-trigger">
        <DropdownToggle color="reveal" size="sm" className="py-0 px-2">
          <FontAwesomeIcon icon="ellipsis-h" />
        </DropdownToggle>
        <DropdownMenu right className="py-0">
          <DropdownItem onClick={()=>handleMove(-1)}>Move Left</DropdownItem>
          <DropdownItem onClick={()=>handleMove(1)}>Move Right</DropdownItem>
          <DropdownItem divider />
          <DropdownItem className="text-danger" onClick={()=>setIsOpenRemoveModal(true)}>Remove</DropdownItem>
        </DropdownMenu>
      </UncontrolledDropdown>
    </div>
  );
};

export default KanbanColumnHeader;
