import { shallowEqual } from "react-redux";
import { getPersonAssignments } from "store/thunks/workflow/tasks";
import { MPerson } from "store/types/models/id";
import { AssignmentWithTaskInfo, TaskWithAssignmentInfo } from "store/types/models/workflow";
import useLoadAndSelect from "../useLoadAndSelect";

function useAssignments(personId: MPerson['_id']) {
    return useLoadAndSelect({
        actionCreator: getPersonAssignments,
        selector: state => (Object.values(state.workflow.tasks) as TaskWithAssignmentInfo[])
                            .filter( t => t.isMyAssignment )
                            .reduce( (assignments, task) => [
                                ...assignments, 
                                ...task.assignments
                                    .filter(a => a.assignees.find(a => a.personId === personId))
                                    .map(a => ({
                                        ...a,
                                        task: {
                                            _id: task._id,
                                            title: task.title
                                        },
                                        stage: task.stage
                                    }))
                                ], [] as AssignmentWithTaskInfo[]
                            ),
        equality: shallowEqual,
        postParams: personId,
        deduplicate: ['assignments', personId]
    })
}

export default useAssignments;