import { ILink, ITask } from 'components/gantt';
import React, { FC } from 'react';
import Config from '../../helpers/config/Config';
import TaskRow from './TaskRow'

interface Props {
  onScroll: (top: number)=>void,
  itemHeight: number,
  selectedItems?: (ITask | ILink)[],
  onSelectItem: (item: ITask)=>void;
  onUpdateTask?: (item: ITask, config: Record<string, any>)=>void;
  nonEditable?: boolean;
  data: ITask[];
  startRow: number;
  endRow: number;
}

const TaskList: FC<Props> = React.memo(({ 
  onScroll,
  itemHeight,
  selectedItems,
  onSelectItem,
  onUpdateTask,
  nonEditable,
  data=[],
  startRow,
  endRow
}) => {
  const ref = React.useRef<HTMLDivElement>(null)
  return (
    <div className="timeLine-side">
      <div className="timeLine-side-title" style={Config.values.taskList.title.style}>
        <div>{Config.values.taskList.title.label}</div>
      </div>
      <div ref={ref} className="timeLine-side-task-viewPort scrollbar" onScroll={()=>onScroll(ref.current!.scrollTop)}>
        <div className="timeLine-side-task-container" style={{height: data.length > 0 ? data.length * itemHeight : 10}}>
          {data?.map( (item, i)=> (
            <TaskRow
              key={i}
              index={i}
              item={item}
              label={item.name}
              top={i * itemHeight}
              itemHeight={itemHeight}
              onUpdateTask={onUpdateTask}
              onSelectItem={onSelectItem}
              nonEditable={nonEditable}
              isSelected={ !!selectedItems?.find( i => i.id === item.id) }
            />
          ))}
        </div>
      </div>
    </div>
  )
})

  export default TaskList;

