import { Attachable, Coordinates3D } from "./Attachable";
import { coordsToMagnitude } from "./Vector";

export interface NewtonObjectOptions {
    friction?: number;
    damping?: number;
    maxSpeed?: number;
    refreshRate?: number;
    centroid?: Partial<Coordinates3D>;
    mass: number;
}

export default class NewtonObject<T extends Attachable = Attachable> {
    public friction = 0;
    public damping = 0;
    public maxSpeed?: number;
    public refreshRate = 60;
    public readonly centroid = {
        x: 0,
        y: 0,
        z: 0
    }
    public readonly velocity = {
        x: 0,
        y: 0,
        z: 0
    }
    private _lastT?: number;
    public mass: number;
    public cutoffSpeed = 1;
    public cutoffForce = 1;
    public forces: T[] = [];
    private motionTimer?: number;
    public onMoved?(position: Coordinates3D, velocity: Coordinates3D): void;

    constructor(options: NewtonObjectOptions) {
        if (!options.mass)
        this.centroid = Object.assign({}, this.centroid, options.centroid);
        this.friction = options.friction || this.friction
        this.damping = options.damping || this.damping
        this.maxSpeed = options.maxSpeed || this.maxSpeed
        this.refreshRate = options.refreshRate || this.refreshRate
        this.mass = options.mass;
    }

    private motion = () => {
        const fNet = { x: 0, y:0, z:0 }
        for (const force of this.forces) {
            const f = force.getForceVector();
            if (f) {
                fNet.x += f.x;
                fNet.y += f.y;
                fNet.z += f.z;
            }
        }
        const speed = coordsToMagnitude( this.velocity )
        const velocityVector = {
            x: speed ? this.velocity.x / speed : 0,
            y: speed ? this.velocity.y / speed : 0,
            z: speed ? this.velocity.z / speed : 0
        }

        // friction force
        const frictionForce = speed > this.cutoffSpeed ? this.mass * this.friction : 0;
        fNet.x -= velocityVector.x * frictionForce;
        fNet.y -= velocityVector.y * frictionForce;
        fNet.z -= velocityVector.z * frictionForce;

        // friction damping force
        const dampingForce = speed > this.cutoffSpeed ? this.mass * this.damping * speed : 0;
        fNet.x -= velocityVector.x * dampingForce;
        fNet.y -= velocityVector.y * dampingForce;
        fNet.z -= velocityVector.z * dampingForce;

        const acceleration = {
            x: fNet.x / this.mass,
            y: fNet.y / this.mass,
            z: fNet.z / this.mass
        }
        const current = Date.now();
        const dT = this._lastT ? (current - this._lastT)/1000 : 0;
        const newVelocity = {
            x: this.velocity.x + acceleration.x * dT,
            y: this.velocity.y + acceleration.y * dT,
            z: this.velocity.z + acceleration.z * dT,
        }
        let newSpeed = coordsToMagnitude(newVelocity)
        if (this.maxSpeed && newSpeed > this.maxSpeed) {
            const reduction = (this.maxSpeed/newSpeed)
            newVelocity.x *= reduction;
            newVelocity.y *= reduction;
            newVelocity.z *= reduction;
            newSpeed = this.maxSpeed;
        }
        this.centroid.x += (this.velocity.x + newVelocity.x) / 2 * dT;
        this.centroid.y += (this.velocity.y + newVelocity.y) / 2 * dT;
        this.centroid.z += (this.velocity.z + newVelocity.z) / 2 * dT;
        Object.assign(this.velocity, newVelocity)
        return fNet
    }

    private motionLoop = () => {
        window.clearTimeout( this.motionTimer )
        const fNet = this.motion();
        this._lastT = Date.now();
        if ( (coordsToMagnitude(fNet) > this.cutoffForce) || (coordsToMagnitude(this.velocity) > this.cutoffSpeed) ) {
            if (this.onMoved) this.onMoved({...this.centroid}, {...this.velocity});
            this.motionTimer = window.setTimeout(this.motionLoop, 1000/this.refreshRate)
        } else {
            this._lastT = undefined;
        }
    }

    public forceChange = () => {
        this.motionLoop()
    }

    public attachForce (attachable: T) {
        if (this.forces.find( f => f === attachable )) return;
        this.forces.push(attachable)
        attachable.attachObject(this)
    }

    public detatchForce (attachable: T) {
        this.forces = this.forces.filter( f => f === attachable )
        attachable.detatchObject()
    }
}